@use '../../../assets/styles/abstracts/colors' as colors;

.home {

  &__title {
    &_general {
      font-size: 60px;
      line-height: 73px;
      font-weight: 700;
      letter-spacing: -0.02em;
      color: #222222;
      text-align: center;
      max-width: 857px;
      margin: 0 auto 40px auto;
    }
  }

  &__tag {
    &_title {
      display: inline-block;
      font-size: 24px;
      line-height: 29px;
      font-weight: 400;
      color: colors.$blue-darker;
      padding: 6px 20px;
      background: rgba(113, 133, 236, 0.3);
      border-radius: 4px;
    }
  }

  /* -----------------------------*/
  &__lead {
    padding: 100px 0;
    margin: 0;    

    &-grid {
      display: grid;
      gap: 28px 79px;
      grid-template-columns: minmax(1fr, 593px) minmax(1fr, 635px);
      grid-template-areas:
      "intro image"
      "links image";
    }

    &-intro {
      grid-area: intro;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }

    &-downloads {
      grid-area: links;
    }

    &-image-container {
      grid-area: image;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }

    &-title {
      font-size: 48px;
      line-height: 58px;
      font-weight: 700;
      letter-spacing: -0.02em;
      padding: 0;
      margin: 0 0 40px;
    }

    &-list {
      list-style: none;
      margin: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;

      &-item {
        font-size: 32px;
        line-height: 39px;
        font-weight: 700;
        letter-spacing: -0.02em;
        padding: 0 0 0 74px;
        margin: 0 47px 20px 0;
        display: block;
        background-image: url("../../../assets/images/icon-check.svg");
        background-repeat: no-repeat;
        background-size: 35px;
        background-position: center left;

        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }

    &-slogan {
      color: colors.$blue;
      font-size: 32px;
      line-height: 39px;
      font-weight: 700;
      letter-spacing: -0.02em;
      padding: 0;
      margin: 0 0 46px 0;
    }

    &-image-wrapper {
      max-width: 100%;
      max-height: 100%;
    }

    &-image {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  /* -----------------------------*/

  &__about {
    padding: 103px 0;

    &-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    &-image-container {
      width: 50%;
    }

    &-text-container {
      width: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &-list {
      list-style: none;
      margin: 0;
      padding: 0;
      width: 100%;
      max-width: 483px;

      &-item {
        font-size: 36px;
        line-height: 44px;
        font-weight: 600;
        color: rgba(34, 34, 34, 0.6);
        text-transform: uppercase;
        text-align: center;
        margin: 0 0 60px 0;
        
        &:first-of-type {
          background-image: url('../../../assets/images/home/about-line-first.svg');
          background-size: auto;
          background-repeat: no-repeat;
          background-position: center 8px;
        }

        &:nth-child(2) {
          color: colors.$blue-darkest;
          font-size: 48px;
          line-height: 58px;
          font-weight: 700;
          position: relative;

          &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 326px;
            height: 204px;
            display: block;
            content: '';
            background: rgba(113, 133, 236, 0.3);
            opacity: 0.75;
            filter: blur(122.422px);
            transform: rotate(-21.2deg);
            z-index: 10;
          }
        }

        &:last-of-type {
          background-image: url('../../../assets/images/home/about-line-last.svg');
          background-size: auto;
          background-repeat: no-repeat;
          background-position: center 13px;
          padding-bottom: 15px;
        }

        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
  }

  /* -----------------------------*/

  &__video {
    padding: 64px 0 46px 0;

    &-cover-wrapper {
      min-width: 100%;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;

      &::after {
        position: absolute;
        top: center;
        left: center;
        display: block;
        border: 3px solid white;
        border-radius: 50%;
        content: '\25B2';
        font-size: 26px;
        padding: 18px 20px;
        color: white;
        z-index: 100;
        transform: rotate(90deg);
        cursor: pointer;
        opacity: 0.7;
      }
    }

    &-cover-wrapper:hover::after {
      opacity: 1;
      border: 5px solid white;
    }

    &-cover {
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
      border-radius: 6px;
      filter: drop-shadow(-8px 24px 35px rgba(14, 6, 37, 0.5));
    }
  }

  /* -----------------------------*/

  &__helpus {
    padding: 100px 0 50px;

    &-content-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    &-text-wrapper {
      max-width: 564px;
      margin-right: 20px;
    }

    &-list {
      list-style: none;
      margin: 0;
      padding: 28px 0;
      display: flex;
      flex-direction: column;
      justify-content: center;

      &-item {
        font-size: 28px;
        line-height: 34px;
        font-weight: 300;
        text-transform: uppercase;
        padding: 0 0 0 74px;
        margin: 0 47px 20px 0;
        display: block;
        background-image: url("../../../assets/images/icon-check-blue.svg");
        background-repeat: no-repeat;
        background-size: 32px 32px;
        background-position: center left;
        
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }

    &-text {
      font-size: 24px;
      line-height: 39px;
      color: colors.$blue-darker;
      margin: 0;
      padding: 0;
    }

    &-image-wrapper {
      max-width: 647px;
    }

    &-image {
      min-width: 100%;
      min-height: 100%;
      max-width: 100%;
      max-height: 100%;
      object-fit: contain;
    }
  }

  &__now {
    padding: 120px 0;

    &-title {
      font-size: 60px;
      line-height: 73px;
      font-weight: 700;
      text-align: center;
      text-transform: uppercase;
      padding: 0;
      margin: 0 0 60px;
    }

    &-apps-list {
      margin: 0;
      padding: 0;
      list-style: none;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 60px 225px;

      &-item {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin: 0;
        padding: 0;
      }
    }

    &-apps-title {
      font-size: 24px;
      line-height: 29px;
      font-weight: 400;
      color: colors.$blue;
      padding: 8px 24px;
      margin: 0 0 20px 0;
      background: rgba(113, 133, 236, 0.1);
      border-radius: 3px;
    }

    &-apps-image-wrapper {
      max-width: 100%;
      min-width: 100%;
    }

    &-apps-image {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    &-apps-subtitle {
      font-size: 18px;
      line-height: 22px;
      font-weight: 600;
      text-align: center;
      color: colors.$grey;
      padding: 0;
      margin: 20px 0 0;
    }
  }

  &__joinus {
    padding: 218px 0;

    &-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }

    &-image-container {
      max-width: 588px;
      padding: 55px;
    }

    &-image {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    &-tags-list {
      list-style: none;
      margin: 0 0 32px;
      padding: 0;
      display: flex;

      &-item {
        font-size: 18px;
        line-height: 22px;
        font-weight: 400;
        padding: 6px 20px;
        margin: 0 16px 0 0;
        color: colors.$blue;
        background: rgba(113, 133, 236, 0.1);
        border-radius: 4px;

        &:last-of-type {
          margin-right: 0;
        }
      }
    }

    &-content-container {
      max-width: 618px;
    }
  
    &-title {
      font-size: 48px;
      line-height: 58px;
      letter-spacing: -0.02em;
      font-weight: 700;
      color: colors.$blue;
      text-transform: uppercase;
      margin: 0 0 40px;
      padding: 0;
    }

    &-text {
      font-size: 18px;
      line-height: 22px;
      font-weight: 400;
      padding: 0;
      margin: 0 0 42px;
    }  
  }

  &__indev {
    padding: 110px 0;

    &-container {
      display: flex;
      align-items: center;
      flex-direction: column;
    }

    &-title {
      font-size: 48px;
      line-height: 58px;
      font-weight: 700;
      letter-spacing: -0.02em;
      color: #222222;
      text-transform: uppercase;
      margin: 0 0 100px;
      padding: 0;
    }

    &-apps-list {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      gap: 40px 80px;
      list-style: none;
      margin: 0;
      padding: 0;

      &-item {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }

    &-apps-title {
      font-size: 22px;
      line-height: 27px;
      font-weight: 600;
      color: #222222;
      margin: 12px 0 0;
      padding: 0 10px;
      text-align: center;
    }
  }
}